.elastic-ip {
  padding: .26rem .3rem;

  .top {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: .2rem;

    .tip {
      color: #9696A3;
      padding: .08rem 0;
      line-height: 1;
    }
  }

  .create {
    line-height: .4rem;
    padding: 0 .16rem;
    color: #fff;
    background: #5C5EE4;
    border-radius: .04rem;
    cursor: pointer;
  }

  table {
    border: 1px solid #d9dbe1 !important;
  }

  .ant-table-tbody>tr>td:first-child,
  .ant-table-thead>tr>th:first-child {
    padding-left: .6rem;
  }


}

/* refund-modal */
.refund-modal {
  width: 6.4rem !important;
  font-family: 'Source Han Sans CN';

  .ant-modal-body {
    padding: .4rem .3rem;
  }

  .modal-title {
    font-size: .18rem;
    color: #040E34;
    line-height: 1;
    margin-bottom: .3rem;
  }

  .config-item {
    margin-bottom: .2rem;
    display: flex;

    &:first-child {
      .row-r {
        padding: .25rem .2rem;
        font-size: .13rem;
        color: #9696A3;
      }

      .l-tit {
        margin-right: .2rem;
      }

      .item {
        display: flex;
        line-height: .14rem;
        margin-bottom: .2rem;

        &:last-child {
          margin-bottom: 0;
        }

        p {
          margin-bottom: 0;
        }
      }

      .r-text {
        p {
          margin-bottom: .1rem;

          &:last-child {
            margin-bottom: 0;
          }
        }
      }
    }

  }

  .row-l {
    line-height: .4rem;
    font-size: .14rem;
    color: #040E34;
    min-width: .8rem;
  }

  .row-r {
    border-radius: .04rem;
    background: #F2F2F7;
    flex: 1;
  }

  textarea,
  .ant-select-selector {
    background: #F2F2F7 !important;
    resize: none;
    border-color: #F2F2F7 !important;
  }

  .money {
    align-items: center;

    .row-l,
    .row-r {
      line-height: 1;
    }
  }

  .modal-footer {
    text-align: center;

    .ant-btn {
      line-height: .4rem;
      height: .4rem;
      min-width: .8rem;
      padding: 0 .16rem;
      border-radius: .04rem;
      margin: 0 .04rem;
    }

    .sure {
      background: #5C5EE4;
      color: #fff;
      border-color: #5C5EE4;
    }

    .cancel {
      border-color: #D9DBE1;
      color: #525666;
    }
  }

  .ant-select-selection-item {
    font-size: .14rem;
    color: #040E34;
  }
}

/* create-ip */
.create-ip {
  .com-tit .tit {
    color: #040E34;
    line-height: 1;
    margin-bottom: 0.32rem;
    font-size: .18rem;
  }

  .com-card {
    padding: 0.56rem 0.3rem 1.45rem 0.3rem;
  }

  .ant-select {
    width: 3.9rem;
    line-height: .44rem;
    height: .44rem;
  }

  .ant-select-selector,
  .ant-select-selection-item {
    height: .44rem !important;
    line-height: .44rem !important;
    border-radius: .04rem !important;
  }

  .s-tip {
    font-size: .14rem;
    color: #9696A3;
    line-height: 1;
    margin-top: .1rem;
  }

  #vpc_package_id {
    margin-bottom: -.3rem;
  }

  .ant-col .item {
    border: 1px solid #D9DBE1;
    border-radius: 0.04rem;
    padding: 0.09rem 0.16rem;
    line-height: 0.28rem;
    position: relative;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: .14rem;
    color: #040E34;
    margin-bottom: .3rem;
  }

  .ant-slider {
    width: 4.5rem;
  }

  .c-active {
    background: #fff !important;
    box-shadow: 0 0 .2rem .05rem rgba(137, 138, 228, .3);

    &.c-hover {
      border-color: transparent !important;
    }

    &::after {
      content: '';
      width: .2rem;
      height: .2rem;
      position: absolute;
      text-align: center;
      line-height: .2rem;
      top: 0;
      right: 0;
      z-index: 2;
      background: url(../../../assets/images/cloud/check.png) no-repeat;
      background-size: 100%;
    }
  }

  .network {
    .item {
      display: flex;
      justify-content: center;
      font-size: .18rem;
      color: #040E34;
    }
  }

  .bw_size {
    font-size: .14rem;
    color: #040E34;

    .ant-input-group-compact {
      display: flex;
      align-items: center;

      .ant-input,
      .ant-input-number {
        width: .96rem;
        line-height: .48rem;
        margin-left: .3rem;
        margin-right: .1rem;
        border-radius: .04rem;
      }
    }
  }

  /* total-box */
  .total-box {
    background: #fff;
    z-index: 20;
    width: 100%;
    height: 1.45rem;
    position: fixed;
    bottom: 0;
    left: 0;
    padding-left: 265px;
    box-shadow: 0 0 .2rem .05rem rgba(137, 138, 228, .3);

    #bot {
      .ant-popover {
        padding: .12rem .17rem;
        background: #fff;

        .discount {
          .ant-input {
            width: 1.8rem;
            line-height: .4rem;
            height: .4rem;
            display: inline-block;
            background: #F2F2F7;
            border-radius: .04rem;
          }

          .close {
            line-height: .4rem;
            padding: 0 .16rem;
            border-radius: .04rem;
            background: #FEB63B;
            font-size: .14rem;
            color: #fff;
            margin-left: .08rem;
            display: inline-block;
            cursor: pointer;
          }
        }

      }
    }

    .discount_code {
      display: inline-block;
      vertical-align: top;
      top: .3rem;
      position: relative;
      cursor: pointer;
    }

    .r-btn {
      display: inline-block;
      margin-left: .06rem;
    }

    .checkout {
      width: 1.6rem;
      line-height: .48rem;
      background: #FCAF55;
      border-radius: .04rem;
      color: #fff;
      text-align: center;
      font-size: .16rem;
      display: inline-block;
      margin-bottom: .14rem;
      cursor: pointer;
    }

    img {
      margin-right: .12rem;
    }

    .price {
      color: #FCAF55;
      font-size: .14rem;
      line-height: 1;

      .num {
        font-size: .32rem;
      }
    }

    .content {
      width: 77.945619%;
      height: 100%;
      margin: 0 auto;
      display: flex;
      justify-content: space-between;
      padding: .2rem 0;
    }

    .item {
      display: flex;
      font-size: .13rem;
      color: #9696A3;
    }

    p {
      margin-bottom: 0;
    }

    .config {
      .ant-select {
        min-width: 1rem;
      }

      .duration {
        position: relative;
      }
    }

    .l-config:first-child {
      margin-right: .4rem;
    }

    .tit {
      font-size: .16rem;
      color: #040E34;
      font-weight: 400;
      margin-bottom: .2rem;
      line-height: 1;
    }

    .des {
      line-height: .28rem;
      display: flex;
      align-items: center;

      img {
        border-radius: .04rem;
      }
    }

    .cycle {
      line-height: .32rem;
      height: .32rem;
      width: 1.5rem;
    }

    .ant-select-selector,
    .ant-select-selection-item {
      line-height: .32rem !important;
      height: .32rem !important;
    }

    .ant-input-number {
      width: .8rem;
      line-height: .32rem;
    }
  }

  .create-btn {
    width: 1.6rem;
    line-height: .48rem;
    height: .48rem;
    border-radius: .04rem;
    background: #5C5DE3;
    color: #fff;
    text-align: center;
    font-size: .16rem;
    margin-top: .2rem;
    cursor: pointer;
  }
}

.size-main {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 0;

  .size-content {
    display: flex;
    flex-direction: row;
    margin-left: .25rem;
    flex-wrap: wrap;

    .active {
      position: relative;

      &::before {
        content: '当前';
        position: absolute;
        right: 0;
        bottom: 0;
        width: .4rem;
        height: .2rem;
        background: #DBDBDD;
        opacity: 1;
        border-radius: 4px 0px 4px 0px;
        text-align: center;
        color: #FFF;
      }

      .item-top,
      .item-bottom {
        color: #DBDBDD !important;
      }
    }

    .size-item {
      width: 1.5rem;
      height: .64rem;
      background: #FFFFFF;
      border: 1px solid #D9DBE1;
      opacity: 1;
      border-radius: 4px;
      display: flex;
      flex-direction: column;
      padding-left: .12rem;
      padding-top: .1rem;
      margin-right: .2rem;
      margin-top: .1rem;
      cursor: pointer;

      .item-top {
        font-size: .16rem;
        line-height: .16rem;
        color: #5C5EE4;
        opacity: 1;
        margin-bottom: .1rem;
      }

      .item-bottom {
        font-size: .16rem;
        font-weight: bold;
        line-height: .16rem;
        color: #525666;
        opacity: 1;
      }
    }
  }
}
@primary-color: #5C5EE4;