.pay-modal {
  width: 6.4rem !important;
}
.pay-modal .ant-modal-header {
  border-radius: 0.12rem 0.12rem 0 0;
  line-height: 1;
  padding: 0.4rem 0.3rem 0.3rem 0.3rem;
  border-bottom: 0;
  font-size: 0.18rem;
  color: #040E34;
}
.pay-modal .ant-modal-content {
  width: 6.4rem !important;
  border-radius: 0.12rem;
}
.pay-modal .ant-modal-body {
  padding-top: 0;
  padding-bottom: 0.4rem;
}
.pay-modal .ant-modal-footer {
  text-align: center;
  border-top: 0;
  padding: 0.16rem 0 0.38rem;
}
.pay-modal .ant-btn {
  width: 0.88rem;
  line-height: 0.4rem;
  height: 0.4rem;
  border-radius: 0.04rem;
  padding: 0;
  font-size: 0.14rem;
}
.pay-modal .ant-btn span {
  line-height: 1;
}
.pay-modal .ant-btn.sure {
  background: #5C5EE4;
  color: #fff;
}
.pay-modal .ant-btn.cancel:hover {
  background: #5C5EE4;
  color: #fff;
}
.pay-modal .cloud-pay-box .item {
  display: flex;
  justify-content: flex-start;
}
.pay-modal .cloud-pay-box .item.first,
.pay-modal .cloud-pay-box .item .ant-select-selector,
.pay-modal .cloud-pay-box .item .ant-select-selection-item {
  line-height: 0.44rem;
  height: 0.44rem;
  font-size: 0.14rem;
  border-radius: 0.04rem;
}
.pay-modal .cloud-pay-box .item .ant-select-selection-item {
  color: #9696A3;
}
.pay-modal .cloud-pay-box .item.first {
  margin-bottom: 0.52rem;
}
.pay-modal .cloud-pay-box .item.last {
  margin-top: 0.32rem;
}
.pay-modal .cloud-pay-box .ant-select {
  width: 5rem;
  display: block;
  height: 0.44rem;
  line-height: 0.44rem;
}
.pay-modal .cloud-pay-box .l-text {
  width: 0.8rem;
  font-size: 0.13rem;
  color: #040E34;
}
.pay-modal .cloud-pay-box .ant-checkbox-wrapper {
  margin-bottom: 0.24rem;
  line-height: 1;
}
.pay-modal .cloud-pay-box .order-num {
  font-size: 0.14rem;
  color: #040E34;
  font-weight: 400;
}
.pay-modal .cloud-pay-box .pay-num {
  font-size: 0.16rem;
  color: #FFA800;
  line-height: 1;
}
.pay-modal .cloud-pay-box .pay-num p {
  margin-bottom: 0;
}
.pay-modal .cloud-pay-box .pay-num .paid {
  font-weight: 500;
  margin-bottom: 0.1rem;
}
.pay-modal .cloud-pay-box .pay-num .balance {
  font-size: 0.13rem;
  margin-left: -0.04rem;
}
.pay-modal .cloud-pay-box .pay-html {
  height: 3rem;
  width: 100%;
  display: flex;
  justify-content: center;
  margin: 0;
  border: 0;
}
